// 引入通用table组件
import moment from 'moment';
import VXETable from '@/components/common/Table.vue';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import SearchBox from '@/components/common/SearchBox';
import DialogSettingColumn from '@/components/common/DialogCustomizeColumns/DialogSettingColumn';
import {
  STATUS_CODE,
  TITLE,
  VARIABLE,
  BTN_TEXT,
  EQUIPMENT_LIVE,
  CONSTANTS_DEVICE_CONFIGURATION
} from '@/utils/constantsFile';
// 导入所需接口
import {
  searchCondetionFunc
} from '@/utils/publicFunction';
import { getRelationshipOffLineState } from '@/api/equipmentstatus';
// import { ExportDeviceLiveExcel } from '@/api/export';
import CustomColumn from '@/components/common/CustomColumn';
export default {
  components: {
    // 加载通用table组件
    VXETable,
    SearchBox,
    CustomColumn,
    DialogSettingColumn
    // tableChange
  },
  props: {
    groupType: String,
    isSearchBoxShow: {
      type: Boolean
    },
    showInfo: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      VARIABLE: VARIABLE,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      BTN_TEXT: BTN_TEXT,
      deviceName: '',
      dialogVisible: false,
      hackReset: false,
      // 查询字段
      srearchList: [],
      orderByData: {},
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // 查询字段
      searchData: [],
      // 显示那个表
      showTable: true,
      // table是否加载
      tableLoadings: false,
      // 控制自定义列模态框显示
      dialogVisibleCustomColumn: false,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumn: [],
      tableColumns: [
        {
          field: 'deviceName',
          title: EQUIPMENT_LIVE.DEVICE_NAME,
          visible: true,
          sortable: true,
          align: 'center'
        },
        {
          field: 'deviceCode',
          visible: true,
          sortable: true,
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_CODE,
          align: 'center'
        },
        {
          field: 'devicePriCode',
          visible: true,
          sortable: true,
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_PRCODE,
          align: 'center'
        },
        {
          field: 'number',
          visible: true,
          sortable: true,
          title: CONSTANTS_DEVICE_CONFIGURATION.DEVICE_EXTEND,
          align: 'center'
        },
        {
          field: 'typeName',
          title: EQUIPMENT_LIVE.DEVICE_TYPE,
          visible: true,
          align: 'center'
        },
        {
          field: 'stateStartTime',
          title: '离线开始时间',
          visible: true,
          align: 'center',
          formatter: function({ cellValue }) {
            if (cellValue) {
              return moment(cellValue).format('YYYY-MM-DD HH:mm:ss');
            } else {
              return '从未上线';
            }
          }
        },
        {
          field: 'stateDurationTime',
          title: '离线持续时长',
          visible: true,
          align: 'center'
        }
      ],
      typeOptions: [],
      groupOptions: [],
      typeValue: '',
      groupValue: '',
      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 20,
        align: 'right',
        pageSizes: [20, 50, 100, 200, 500, 1000],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      // 是否显示自定义列对话框
      savedSearchCondition: []
    };
  },
  computed: {
  },
  created() {
    // 调用列表查询接口
    this.findList();
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      try {
        this.tableLoadings = true;

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: [],
          orderBy: this.orderByData
        };

        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        conditionJson.size = this.tablePages.pageSize;
        conditionJson.current = this.tablePages.currentPage;

        const res = await getRelationshipOffLineState(
          {}, conditionJson
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 查询
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    search() {
      this.onTablePageChanges(1, 10);
    },
    /**
     * @description: 重置
     * @param {type}
     * @return:
     * @author: CH
     */
    reset() {
      this.onTablePageChanges(1, this.tablePages.pageSize);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      var that = this;
      try {
        this.tableLoadings = true;

        var conditionJson = {
          conditionJson1: [],
          operatorGroup: 1,
          conditionJson2: [],
          orderBy: this.orderByData
        };

        if (this.savedSearchCondition.length > 0) {
          conditionJson = searchCondetionFunc(...this.savedSearchCondition);
        }

        conditionJson.size = 99999999;
        conditionJson.current = 1;

        const res = await getRelationshipOffLineState(
          {},
          conditionJson
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var customColumns = [];

          customColumns = that.tableColumns;

          // 过滤需要展示的列
          var showArr = [];
          for (var i = 0; i < customColumns.length; i++) {
            if (customColumns[i].type !== 'radio' && customColumns[i].type !== 'checkbox') {
              showArr.push(customColumns[i].field);
            }
          }

          this.$refs.xGrid.exportData('离线设备', res.rows, showArr);
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:表格排序
     * @return:
     * @author: CH
     */
    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      this.onTablePageChanges(1, this.tablePages.pageSize);
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: CH
     */
    changeTable() {
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.hackReset = true; // 重建组件
      });
    },
    /**
     * @description: 刷新表格表头和表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableColumns() {
      // 刷新表格
      this.refreshTableData();
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
        this.tablePages.currentPage = 1;
      }
      this.findList();
    }
  }
};
