-->
<template>
  <el-dialog
    class="customize-column-dialog"
    :show-close="false"
    :visible.sync="isDialogShow"
    :close-on-click-modal="false"
  >
    <!-- 下面一行待删除 -->
    <!-- @close="clearDefaultTableColumns" -->
    <span slot="title">
      <span class="customize-column-dialog__title">{{dialogTitle}}</span>
      <span class="customize-column-dialog__prompt">勾选需要显示的列，拖动列名进行排序</span>
    </span>

    <div style="height:400px;overflow:auto">
        <!-- 行可上下拖动 -->
    <vue-draggable
      animation="380"
      ghostClass="ghost"
      v-model="tempTableColumns"
      handle=".move-row"
      scroll
    >
      <li
        v-for="(column, index) in tempTableColumns"
        class="column-row"
        :key="index"
      >
        <el-checkbox
          class="column-row__checkbox"
          v-model="column.visible"
          :disabled="column.required"
        >
        </el-checkbox>
        <!-- 列名 -->
        <span class="column-row__tilte" :class="column.required ? '' : 'move-row'">{{ column.title }}</span>
        <!-- 宽度输入框 -->
        <el-input
          class="column-low__input"
          size="mini"
          v-model="column.width"
        >
        </el-input>
        <!-- 单位 -->
        <span class="column-row__unit">px</span>
      </li>
    </vue-draggable>
    </div>

    <span slot="footer" class="dialog-footer">
      <el-button
        plain
        class="custom-button"
        @click="handleResetEvent"
      >
        恢复默认
      </el-button>
      <el-button
        plain
        class="custom-button"
        @click="handleCloseEvent"
      >
        关闭
      </el-button>
      <el-button
        type="primary"
        class="custom-button"
        @click="handleSaveEvent"
      >
        保存
      </el-button>
    </span>
  </el-dialog>
</template>

<script>
import VueDraggable from 'vuedraggable';

export default {
  name: 'dialogCustomizeColumnsDialogSettingColumn',
  components: {
    VueDraggable
  },
  props: {
    // 父页面传入的列配置
    pageColumns: {
      type: Array,
      default: () => [],
      required: true
    },
    // 父页面决定是否显示对话框
    showSettingDialog: {
      type: Boolean,
      default: () => false
    },
    defaultColumns: {
      type: Array,
      default: () => [],
      required: true
    }
  },

  watch: {
    pageColumns: {
      handler(val) {
        this.tempTableColumns.splice(0);
        const columns = [];
        for (const column of val) {
          // 应该不能对操作列进行编辑，因为当前操作列全部统一了
          if (column.field && column.title) {
            columns.push({
              field: column.field,
              title: column.title,
              width: column.minWidth || '100',
              visible: column.visible,
              required: column.required
            });
          }
        }
        this.tempTableColumns.push(...columns);
        this.initTableColumns.push(...columns);
      },
      immediate: true,
      deep: true
    },
    defaultColumns: {
      handler(val) {
        this.defaultTableColumns.splice(0);
        const columns = [];
        for (const column of val) {
          // 应该不能对操作列进行编辑，因为当前操作列全部统一了
          if (column.field) {
            columns.push({
              field: column.field,
              title: column.title,
              width: column.minWidth || '100',
              visible: column.visible,
              required: column.required
            });
          }
        }
        this.defaultTableColumns.push(...columns);
      },
      immediate: true,
      deep: true
    },
    showSettingDialog: {
      handler(val) {
        this.isDialogShow = val;
      },
      immediate: true
    }
  },

  data() {
    return {
      dialogTitle: '自定义列',
      isDialogShow: false,
      // 保存修改后最新的列配置
      tempTableColumns: [],
      // 保存页面初始化时的配置
      initTableColumns: [],
      // 保存默认的列配置
      defaultTableColumns: []
    };
  },
  methods: {
    /**
     * @description: 对话框关闭时，清空保存的列配置
     * @param 无
     * @return: void
     * @author: cyb
     */
    /* clearDefaultTableColumns() {
      this.defaultTableColumns.splice(0);
      this.tempTableColumns.splice(0);
    }, */

    /**
     * @description: “保存”按钮点击事件
     * @param 无
     * @return: void
     * @author: cyb
     */
    handleSaveEvent() {
      this.$emit('saveMyColumnSetting', this.tempTableColumns);
    },

    /**
     * @description: “关闭”按钮点击事件
     * @param 无
     * @return: void
     * @author: cyb
     */
    handleCloseEvent() {
      const len = this.tempTableColumns.length;

      // 记录tempTableColumns在对话框关闭时是否有改动
      let tempColumnsHasNoChange = true;
      for (let i = 0; i < len; i++) {
        const tempColumn = this.tempTableColumns[i];
        const initColumn = this.initTableColumns[i];
        if (tempColumn.field !== initColumn.field) {
          tempColumnsHasNoChange = false;
          break;
        }

        if (tempColumn.width !== initColumn.width) {
          tempColumnsHasNoChange = false;
          break;
        }

        if (tempColumn.visible !== initColumn.visible) {
          tempColumnsHasNoChange = false;
          break;
        }
      }

      // console.log(tempColumnsHasNoChange);

      // 表示对话框关闭时，改动了tempTableColumns
      if (!tempColumnsHasNoChange) {
        const columns = [];
        for (const column of this.pageColumns) {
          if (column.field) {
            columns.push({
              field: column.field,
              title: column.title,
              width: column.minWidth || '100',
              visible: column.visible,
              required: column.required
            });
          }
        }

        // console.log(this.tempTableColumns);
        // 将tempTableColumns重置为改动之前的状态
        this.tempTableColumns.splice(0);
        this.tempTableColumns.push(...columns);

        // console.log(this.tempTableColumns);
      }

      this.$emit('update:showSettingDialog', false);
    },

    /**
     * @description: “恢复默认”按钮点击事件
     * @param 无
     * @return: void
     * @author: cyb
     */
    handleResetEvent() {
      const columns = [];
      for (const column of this.defaultColumns) {
        if (column.field) {
          columns.push({
            field: column.field,
            title: column.title,
            width: column.minWidth || '100',
            visible: column.visible,
            required: column.required
          });
        }
      }
      // 将tempTableColumns重置为改动之前的状态
      this.tempTableColumns.splice(0);
      this.tempTableColumns.push(...columns);
    }
  }
};
</script>

<style lang="less" scoped>
// 自定义列对话框样式
.customize-column-dialog /deep/ .el-dialog__header {
  padding: 0 10px;
}

// 对话框title
.customize-column-dialog__title {
  margin-right: 20px;
  font-weight: bold;
  font-size: 16px
}

.customize-column-dialog__prompt {
  font-size: 13px;
  color:#afafaf;
}

// 对话框列表的行样式
.column-row {
  display:flex;
  justify-content: flex-start;
  align-items:center;
  height: 40px;
  border: 1px solid #dedddd;
}

.column-row + li {
  border-top: none;
}

// checkbox样式
.column-row__checkbox {
  margin-left: 10px;
  margin-right: 15px
}

// 列名样式
.column-row__tilte {
  // 内部
  display: flex;
  justify-content: flex-start;

  // 相对于行来说
  flex: 1;
}

// 输入框样式
.column-low__input {
  width: 10%;
  margin-right: 10px;
}

// 单位样式
.column-row__unit {
  min-width: 30px;
}

// 移动时，鼠标变为十字箭头
.move-row {
  cursor:move;
}
</style>
